.p {
    height: 100%;
    display: flex;
    flex-direction: column;
    /* background-color: red; */
    align-items: center;
    box-sizing: border-box;
}

.p-title {
    flex: 1;
    font-size: 3rem;
}

.card-container {
    width: 95%;
    flex: 2;
    /* border: 1px solid yellow; */
    display: flex;
    flex-wrap: wrap;
}

.card {
    width: 30%;
    border-radius: 10px;
    margin: 5px 10px;
    display: flex;
    flex-direction: column;
    /* border: 1px solid #333; */
    transition: 0.6s ease-in-out;
    overflow: hidden;
    padding: 3px;
    box-sizing: border-box;
}

.c-design{
    margin-bottom: 5px;
    margin-top: 0;
    height: 15px;
    width: 100%;
    background-color: rgb(243,242,242);
    display: flex;
    align-items: center;
    border-radius: 10px 10px 0 0 ;
    display: flex;
    align-items: center;
}

.c-circle{
    margin-left: 10px;
    padding: 2px ;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: rgb(220, 214, 214);
}

.card:hover {
    box-shadow: 18px 18px 37px 7px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 18px 18px 37px 7px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 18px 18px 37px 7px rgba(0, 0, 0, 0.75);
}

.card-img {
    height: 100%;
    width: 100%;
    flex: 1;
}

.card-info {
    flex: 1;
    position: relative;
}


.p-img {
    width: 100%;
    border-radius: 10px;
}

.c-title {
    color: rgb(255, 68, 0);
}

.c-desc {
    color: rgb(59, 59, 59);
    font-weight: 500;
}

.btn {
    width: 25%;
    padding: 5px;
    font-size: 1rem;
    margin: 5px 10px;
    border-radius: 5px;
    background-color: whitesmoke;
    transition: 1s ease;
    border-color: transparent;
}

.btn:hover {
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
    border-color: grey;
}

.link {
    text-decoration: none;
    color: orangered;
}

@media screen and (max-width: 600px){
    .p{
       margin-top: 2rem;
        
    }
    .card-container{
        flex-direction: column;
        align-items: center;
        
    }
    .card{
        width: 80%;
    }
    .card:hover {
        box-shadow: none
    }
    .btn:hover {
        box-shadow: none;
        border-color: grey;
    }
    
}

@media screen and (max-width: 400px){
    .p{
        margin-top: 1rem;
    }
    .card:hover {
        box-shadow: none
    }
    .btn:hover {
        box-shadow: none;
        border-color: grey;
    }
}