* {
    margin: 0;
    padding: 0;
}

.i {
    display: flex;
    /* background-color: hsl(162, 82%, 80%, 0.1); */
    height: 100vh;


}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
}

.i-intro {
    font-size: 2rem;
    font-weight: 300;
}

.i-name {
    font-size: 4rem;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25% {
        transform: translateY(-50px);
    }

    50% {
        transform: translateY(-100px);
    }

    75% {
        transform: translateY(-150px);
    }

    100% {
        transform: translateY(-200px);
    }

}

.i-title-item {
    height: 50px;
    font-size: 2rem;
    font-weight: bold;
    color: rgb(243, 85, 28);
    display: flex;
    align-items: center;
}

.hire-me{
    margin-top: 20px;
}

.hire-me-btn{
    text-decoration: none;
    background-color: rgb(243, 85, 28);
    padding: 6px;
    font-size: 1.5rem;
    border-radius: 10px;
    color: white;
    box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
    -webkit-box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
    -moz-box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
}

.i-desc {
    margin-top: 10px;
    font-size: 1.1rem;
}

.i-img {
    width: 70%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    right: 0;
}

.i-bg {
    clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background: rgb(227, 94, 45);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

@media screen and (max-width: 600px){
    .i{
       flex-direction: column;
    }

    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }

    .i-desc{
        display: none;
    }
}