 /* this changed ? */
.a{
     height: 100vh;
    display: flex;
    align-items: center;
    
}

.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    margin-top: 200px;
  
   
}

.a-right{
    flex: 1;    
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
}


.a-card{
    width: 60%;
    height: 50vh;
    border-radius: 30px;
}

.a-img{
    width: 60%; 
    object-fit: cover;
    position: absolute;
    top: 50px;
    left: 90px;
}

.a-desc{
    width: 80%;
    padding: 5px;
}

@media screen and (min-width: 321px) and (max-width: 600px){
    .a{
        height: 100%;
        flex-direction: column;
        text-align: center;
        margin-top: 1rem;
    }

    .a-left{
        width: 100%;
        margin-top: 10px;
    }
    .a-card{
        width: 55%;
        height: 25vh
    }
    .a-card.bg{
        top: 60px;
        left: 60px;
    }

    .a-right{
        /* margin-top: -210px; */
        margin-top: 2.5rem;
        padding: 20px;
    }
    .a-desc{
       margin: auto;
    }
}

@media screen and (max-width: 320px){
    .a{
        height: 100%;
        flex-direction: column;
        text-align: center;
        margin-top: -400px;
    }
    .a-desc{
        margin: auto;
     }
}
