.t{
    height: 25px;
    width: 50px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: #fff;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: space-around
}

.t-icon{
    width: 20px;
    height: 20px;
}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
}