.contact{
    height: 100%;
    position: relative;
}

.contact-bg{
    width: 20px;
    height: 100%;
    background-color: rgb(227, 94, 45);
    position: absolute;
}

.contact-wrapper{
    padding: 50px;
    display: flex;
}

.contact-left{
    flex: 1;
}

.contact-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact-title{
    font-size: 3.5rem;
    width: 80%;
}

.contact-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0;
}

.contact-desc{
    font-size: 1.2rem;
    font-weight: 600;
}

form{
    width: 70%;
    margin: 20px auto;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
    -webkit-box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
    -moz-box-shadow: 17px 14px 29px 4px rgba(0,0,0,0.75);
}

input{
    width: 50%;
    height: 50px;
    border: none;
    border: 1px solid black;
    border-radius: 5px 0 5px 0;
    margin: 10px 0;
    font-size: 0.8rem;
    padding-left: 10px;
}

textarea{
    width: 80%;
    margin: 10px 0;
    font-size: 0.8rem;
    padding-left: 10px;
}

button{
    border: none;
    padding: 15px;
    background-color: rgb(227, 94, 45);
    color: white;
    font-weight: 500;
    cursor: pointer;
    font-size: .8rem;
}
.email-received{
    color:  rgb(227, 94, 45);
    margin-top: 5px;
}

@media screen and (max-width: 600px){
    
    .contact-wrapper{
       flex-direction: column;
    }

    .contact-left{
        margin-top: 10px;
    }

    .contact-info-item{
        margin: 10px -10px;
    }

    form{
        width: 100%;
        padding: 0 10px;
    
    }
    .contact-desc{
        text-align: center;
        margin-top: 10px;
    }
    .contact-title{
        margin-top: -75px;
    }
    input{
        height: 30px
    };
}
